<!-- template for the modal component -->
<template>
  <div class="modal-mask">
    <div class="modal-wrapper">
      <div class="modal-container">
         <div class="modal-user">
          <slot name="user">
            <input  class="input" type="text" name="username" placeholder="mail">
          </slot>
        </div>

        <div class="modal-passwd">
          <slot name="passwd">
            <input type="text" name="passwd" placeholder="passwd">
          </slot>
        </div>

        <div class="modal-confirm">
          <slot name="footer">
            <button class="modal-default-button" @click="$emit('close')">
              登录
            </button>
          </slot>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'v-login',
    props: {
      data: {
        type: Object
      }
    },
    data: {
      showModal: false

    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus">
  @import "login.styl"
</style>

