<template>
  <div class="bottom">
    <span ref="hot" class="live" v-on:click="onHotClick">热门</span>
    <span ref="live" class="live" v-on:click="onLiveClick">直播</span>
    <span ref="vod" class="vod" v-on:click="onVideoClick">视频</span>
    <span ref="my" class="my" v-on:click="onMyClick">我的</span>
  </div>
</template>

<script type="text/ecmascript-6">
  export default
  {
    name: 'v-bottom',
    components: {
    },
    data() {
      return {
        show: false
      };
    },
    computed: {
    },
    mounted() {
      this.$refs.vod.style.fontSize = '16px';
    },
    methods: {
      onHotClick() {
        this.$refs.hot.style.fontSize = '16px';
        this.$refs.live.style.fontSize = '14px';
        this.$refs.vod.style.fontSize = '14px';
        this.$refs.my.style.fontSize = '14px';
      },
      onLiveClick() {
        this.$refs.hot.style.fontSize = '14px';
        this.$refs.live.style.fontSize = '16px';
        this.$refs.vod.style.fontSize = '14px';
        this.$refs.my.style.fontSize = '14px';
      },
      onVideoClick() {
        this.$refs.hot.style.fontSize = '14px';
        this.$refs.live.style.fontSize = '14px';
        this.$refs.vod.style.fontSize = '16px';
        this.$refs.my.style.fontSize = '14px';
      },
      onMyClick() {
        this.$refs.hot.style.fontSize = '14px';
        this.$refs.live.style.fontSize = '14px';
        this.$refs.vod.style.fontSize = '14px';
        this.$refs.my.style.fontSize = '16px';
      }
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus">
  @import "bottom.styl";
</style>



